import React from 'react';

class Phones extends React.Component {
  state = {
    phones: [],
  }

  componentWillMount() {
fetch('https://mate-academy.github.io/phone-catalogue-static/api/phones.json')
    .then(response => response.json())
    .then(phones => {
      this.setState({
        phones
      })
    })
  }

  render() {
    const {phones} = this.state;
    return (
      <div>
      {/* {<img src={require('./img/phones/motorola-xoom-with-wi-fi.0.jpg')} />} */}
      <h1>Phone page</h1>
      <ul>
        {phones.map(phone => (
          <li
            key={phone.phoneId}
          >
            {phone.name}
            {/* <img src={require(phone.imageUrl)} /> */}
          </li>
        ))}
      </ul>
      {console.log(phones.length)}
      </div>
    )
  };
}
export default Phones;
