import React from 'react';
import './App.css';
import Phones from './Phones'
import Home from './Home';
import NotFoundPage from './NotFoundPage';
import { Route, NavLink, Switch } from 'react-router-dom';

class App extends React.Component {
  state = {

  };

  render() {

    return (
      <div className="header">
        <nav>
          <ul>
            <li className="tabs__item"><NavLink to="/" exact>Home</NavLink></li>
            <li className="tabs__item"><NavLink to="/phones">Phones</NavLink></li>
          </ul>
        </nav>

        <Switch>
          <Route path="/" exact component={Home} />
          <Route exact path="/phones/" render = {({ match }) =>
            <Phones/> }
          />
          <Route path="*" component={NotFoundPage} />
          <Route path="phones/:phoneId" component={Phones} />
        </Switch>
      </div>
    );
  }
}

export default App;
